.splashWrapper {
    width: 60vw;
    height: 100vh;
    padding: 0 20vw 0 20vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    background-image: url(../pages/splash/ky7.jpg);
    filter: grayscale(100%);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    .overlay {
        width: 100%;
        height: 100%;
        background-image: url(data:image/png;base64,);
        background: radial-gradient(circle, rgba(255, 255, 255, 0.7) 20%, rgba(255, 255, 255, 1) 90%);
        // opacity: 1;
        z-index: -1;
        position: absolute;
    }

    .logo {
        // width: 300px;
        width: 32vh;
        max-width: 60vw;
        margin: 0 0 7vh 0;
    }

    h1 {
        text-align: center;
        margin: 0 0 10vh 0;
        text-transform: uppercase;
    }

    .container {
        h3 {
            margin: 0 0 5px 0;
            text-transform: uppercase;
        }

        .email{
            padding: 15px 0 5px 0;
        }

        .socialIcons {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;

            img {
                height: 25px;
                padding: 0 10px 0 15px;
            }
        }
    }
}
